.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 20%;
  margin: 10px;
  border-radius: 8px;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.container {
  padding: 2px 16px;
}

.dishes-aligner {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  overflow: hidden;
  flex: auto;
}

.specials {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 5rem;
}

button.online-menu {
  padding: 12px 14px;
  border-radius: 4px;
  background-color: #f4ce14;
  color: black;
  font-weight: 700;
  border: none;
  margin-right: 2rem;
  margin-bottom: 1rem;
}

img.cuisine-img {
  width: 100%;
  height: 200px;
  border-radius: 8px;
}

.testimonials-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.testimonial {
  padding: 30px 25px;
  box-shadow: 0px 0px 5px 1px lightslategrey;
  border-radius: 10px;
  margin: 1rem;
}

.testimonial-header {
  margin-left: 2rem;
  text-align: center;
  font-weight: 500;
}

.confirm span {
  color: blue;
}

.testimonial-author {
  text-align: end;
  color: rgb(247, 64, 4);
  font-size: 20px;
  font-weight: 600;
}

.testimonial-text {
  font-weight: 600;
  font-size: medium;
}

input,
label,
select {
  width: 90%;
  margin: 15px;
  font-weight: 600;
}

.btnReceive {
  text-align: center;
  margin-top: 2rem;
}

button:disabled {
  background-color: lightgrey;
}

.formField {
  padding: 50px 20px;
}

i.fa.fa-star {
  color: gold;
  font-size: 20px;
}

.confirmed-booking {
  margin-top: 6rem;
  height: 90%;
  background-color: #f4ce14;
  padding: 10px 12px;
}

#reservation h1 {
  margin-top: 5rem;
  text-align: center;
}

label:after {
  content: ' *';
  color: red;
}

#img-overlay-aligner {
  width: 30%;
}